
$black:#101B24;
$ivory:#F8F4E7;
$blown:#525053;
$green:#274000;
$blue:#29516e;
$navy:#0d2030;
$skybule:#bdd6ce;
$skybule_clear:rgba(189,214,206,0.7);

@mixin media_PC($width-media:1390px){
    @media only screen and
      (min-width:$width-media){
      @content;
    }
  }
  
  @mixin media_tablet($width-media:768px){
    @media only screen and
      (min-width:$width-media){
      @content; 
      }
    }    
html {
    font-size: 62.5%;
    font-family: 'Shippori Mincho', serif;
    color:$black;
    

}